@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,600&display=swap);
body{
  margin: 0;
  padding: 0;
  outline: 0;
  
}

*{
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}
.menu-main{width:100%;display:flex;justify-content:space-between;align-items:center}.menu-main div{padding:2%}.menu-main div .LINK{text-decoration:none;cursor:pointer;white-space:nowrap;margin-right:20px;opacity:.8}.menu-main div .LINK:hover{opacity:1}@media only screen and (max-width: 800px){.menu-main div .LINK{margin-right:10px;margin-left:5px}}

@-webkit-keyframes deletePopop{from{margin-left:100%}to{margin-left:1.8%}}@keyframes deletePopop{from{margin-left:100%}to{margin-left:1.8%}}@-webkit-keyframes deletePopopWide{from{margin-left:100%}to{margin-left:60%}}@keyframes deletePopopWide{from{margin-left:100%}to{margin-left:60%}}.read-main{min-height:100vh;width:100%;padding:1% 0}.read-main *{-webkit-transition:all 1s;transition:all 1s}.read-main .unwork{display:none}.read-main .delete{box-shadow:3px 7px 33px 3px rgba(0,0,0,0.75);width:95%;-webkit-animation:deletePopop 1s;animation:deletePopop 1s;margin-left:1.8%;margin-bottom:50px;margin-top:20px;display:flex;justify-content:center;flex-direction:column;align-items:center;text-align:center;flex-wrap:nowrap;padding:2%;-webkit-transition:all .7s;transition:all .7s}.read-main .delete div button{background-color:transparent;border:none;font-size:1em;padding:2%}.read-main .delete div .first-btn-delete{border:1px solid #d63031;color:#d63031}.read-main .delete div .first-btn-delete:hover{outline:none;background-color:#d63031;color:#dfe6e9;-webkit-transition:.5s all;transition:.5s all}.read-main .delete div .second-btn-delete{margin-left:2%;margin-top:4%;border:1px solid #0984e3;color:#0984e3}.read-main .delete div .second-btn-delete:hover{outline:none;background-color:#0984e3;color:#dfe6e9;-webkit-transition:.5s all;transition:.5s all}.read-main h3{text-align:center}.read-main .read-table table{width:100%;border-collapse:collapse;text-align:center}.read-main .read-table table tr{width:30%}.read-main .read-table table tr td{padding:20px}.read-main .read-table table tr td .link{cursor:pointer}.read-main .read-table table tr th{width:30vw;white-space:nowrap;padding:20px;font-size:1.08em;margin:2%}@media (min-width: 900px){.read-main .delete{width:40%;margin-left:58%;-webkit-animation:deletePopopWide 1s;animation:deletePopopWide 1s}}

.read-main{min-height:100vh;width:100%;padding:1% 0}.read-main h3{text-align:center}.read-main .create-form{width:70%;margin-left:50%;-webkit-transform:translate(-50%, 0);transform:translate(-50%, 0);padding:2%}.read-main .create-form .Create-Btn{margin-left:5%}.read-main .create-form button{color:#dfe6e9;border:1px solid #dfe6e9;background-Color:transparent;font-Size:1.1em;cursor:pointer;font-weight:bold;height:31px}.read-main .create-form button:hover{background-color:rgba(255,255,255,0.5)}.read-main .create-form .content-sexo{display:flex;align-items:center}.read-main .create-form .content-sexo p{margin-left:2%}.read-main .create-form label *{margin:1% 0}.read-main .create-form label .styledInput{font-size:1.4em;outline:none;margin-top:.5%;background-color:transparent}.read-main .create-form label .styledInput::-webkit-input-placeholder{color:rgba(255,255,255,0.6)}.read-main .create-form label .styledInput::-moz-placeholder{color:rgba(255,255,255,0.6)}.read-main .create-form label .styledInput:-ms-input-placeholder{color:rgba(255,255,255,0.6)}.read-main .create-form label .styledInput::-ms-input-placeholder{color:rgba(255,255,255,0.6)}.read-main .create-form label .styledInput::placeholder{color:rgba(255,255,255,0.6)}@media (max-width: 800px){.read-main .create-form{width:100%}.read-main .create-form label *{margin:4% 0}.read-main .create-form label .styledInput{font-size:1em}}

